<template>
  <div>
    <div id="loadingContainer" v-if="!ready">
      <svg
        style="
          position: absolute;
          top: 50%;
          left: 50%;
          width: 48px;
          height: 48px;
          transform: translate(-50%, -50%);
        "
        version="1.1"
        id="L9"
        x="0px"
        y="0px"
        viewBox="0 0 100 100"
        enable-background="new 0 0 0 0"
        xml:space="preserve"
      >
        <path
          fill="#71d9c2"
          d="M73,50c0-12.7-10.3-23-23-23S27,37.3,27,50 M30.9,50c0-10.5,8.5-19.1,19.1-19.1S69.1,39.5,69.1,50"
          transform="rotate(188.223 50 50)"
        >
          <animateTransform
            attributeName="transform"
            attributeType="XML"
            type="rotate"
            dur="1s"
            from="0 50 50"
            to="360 50 50"
            repeatCount="indefinite"
          ></animateTransform>
        </path>
      </svg>
    </div>
    <div id="container" v-else style="user-select: none">
      <div id="topYM">
        <div id="topYear"></div>
        <div id="topMonth"></div>
      </div>
      <div id="top">
        <div id="topContainer"></div>
      </div>
      <div id="bottom">
        <div id="left"></div>
        <div id="middle">
          <div
            id="middleContainer"
            @swiping.stop="swipingMiddle"
            @swipeend.stop="swipeendMiddle"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Calendar from "@/assets/js/calendar.js";
import calendarTools from "@/assets/js/calendar.tools.js";
import lessonTools from "@/assets/js/lesson.tools.js";
import Vue from "vue";
import VueTouch from "vue-touchjs";
Vue.use(VueTouch);
export default {
  props: {
    originTimeLength: {
      type: Number,
      default: 30
    },
    originGridCount: {
      type: Number,
      default: 2
    },
    lessonDays: {
      type: Number,
      default: 3
    },
  },
  data() {
    return {
      ready: true,

      d: new Date(),
      currentYear: "",
      currentMonth: "",
      currentDate: "",
      currentFormat: "",
      currentTimestamp: "",
      // lessonDays: 3,
      firstFormatStr: "",
      lastFormatStr: "",
      movable: true,
      selectedDayClickable: true,
      // originTimeLength: 60, //15、30、45、60、75、90、105、120
      // originGridCount: 4,
      startIndex: 24,
      endIndex: 0,
      data: [],
      mergedData: [],
      allDayData: [],
      testData: [],
      isLoaded: false,
      startFromTimestamp: 0,
      w: 0,
      marginLeft: 0,
      moveType: 0, // 0 静止，1 上下，2 左右

      event: "",
    };
  },
  created() {
    let self = this
    self.currentYear = self.d.getFullYear();
    self.currentMonth = self.d.getMonth() + 1;
    self.currentDate = self.d.getDate();
    self.currentFormat = lessonTools.getFormatYYMMDD(self.d);
    self.currentTimestamp = new Date(self.currentFormat).getTime();

    console.log("currentFormat: ", self.currentFormat);
    self.endIndex = 96 - self.originGridCount;
    self.startFromTimestamp = self.d.getTime();
  },
  mounted() {
    let self = this;
    lessonTools.setVariable(this);

    /* start ------ */

    lessonTools.init(0, self.data, self.startFromTimestamp); // 灰

    self.w = $("#middleContainer").width() / 3;
    self.marginLeft = parseFloat($("#middleContainer").css("marginLeft"));
    $("#loadingContainer").remove();
    lessonTools.addCurrentDayMark();

    self.getAgenda([], 0, self.originTimeLength);

    /* end ------ */

    document.addEventListener("touchmove", function (e) {
      // e.stopPropagation();
      // e.preventDefault();
    });
    document.addEventListener("touchstart", function (e) {
      // e.stopPropagation();
      // e.preventDefault();
    });

    $(document).off('click').on("click", ".middle-day-time", function (ev) {
      ev.stopPropagation();
      ev.preventDefault();
      let el = $(ev.target);
      let dayIndex = el.parent().index(),
          timeIndex = el.index(),
          yymmdd = $(".day").eq(dayIndex).data("ymd"),
          d = new Date(yymmdd),
          timeStart = d.getTime() + timeIndex * 15 * 60 * 1000,
          timeEnd =
          d.getTime() + (timeIndex + self.originGridCount) * 15 * 60 * 1000,
          timeStartHour = new Date(timeStart).getHours(),
          timeEndHour = new Date(timeEnd).getHours();

      let childrenCount = el.children().length;
      console.log(el.children())

      let startHourNumber =
        timeIndex % 4 == 0
          ? timeStartHour
          : (timeIndex - 1) % 4 == 0
          ? timeStartHour + 0.25
          : (timeIndex - 2) % 4 == 0
          ? timeStartHour + 0.5
          : timeStartHour + 0.75;
      let endHourNumber =
        timeIndex % 4 == 0
          ? timeEndHour
          : (timeIndex - 1) % 4 == 0
          ? timeEndHour + 0.25
          : (timeIndex - 2) % 4 == 0
          ? timeEndHour + 0.5
          : timeEndHour + 0.75;

      let timestamp =
        new Date(yymmdd).getTime() + startHourNumber * 60 * 60 * 1000;

      $(".selected-day-time").remove();
      if (childrenCount > 0) {
        for (let i = 0; i < childrenCount; i++) {
          if (el.children().eq(i).hasClass("middle-day-event-gray")) {
            el.children().eq(0).append("<div class='selected-day-time'></div>");
            break;
          } else {
            if (i == childrenCount - 1) {
              el.append("<div class='selected-day-time'></div>");
            }
          }
        }
      } else {
        el.append("<div class='selected-day-time'></div>");
      }

      if (timeStart < new Date().getTime()) {
        if (childrenCount > 0) {
          console.log(1)
          if (
            childrenCount == 1 &&
            $(".selected-day-time").eq(0).prev().attr("id") == "currentTimeMark"
          ) {
            console.log(2, self.originGridCount)
            $(".selected-day-time").eq(0).addClass("selected-past-day-time");
            $(".selected-day-time")
              .eq(0)
              .css("height", self.originGridCount * 22 - 4);
            // 此时寻找最近的可用时间
            lessonTools.findRecentAvailableTime($(".selected-day-time").eq(0));
          } else {
            console.log(3)
            $(".selected-day-time").eq(0).addClass("selected-event-day-time");
          }
        } else {
          console.log(4)
          $(".selected-day-time").eq(0).addClass("selected-past-day-time");
          $(".selected-day-time")
            .eq(0)
            .css("height", self.originGridCount * 22 - 4);
          // 此时寻找最近的可用时间
          lessonTools.findRecentAvailableTime($(".selected-day-time").eq(0));
        }
      } else {
        console.log(5)
        if (childrenCount > 0) {
          console.log(6)
          $(".selected-day-time").eq(0).addClass("selected-event-day-time");
        } else {
          console.log(7, self.originGridCount)
          $(".selected-day-time")
            .eq(0)
            .css("height", self.originGridCount * 22 - 4);
        }

        if (el.children().eq(0).data("length") == 1440) {
          console.log("block: ", 0);
          // TODO: return 0
        } else {
          console.log(8)
          // 此时执行规避冲突的操作
          lessonTools.avoidConflictTime($(".selected-day-time").eq(0));
        }

        // 此时执行规避冲突的操作
        // lessonTools.avoidConflictTime($(".selected-day-time").eq(0));
      }
    });

    // $(document).on("click", ".selected-day-time", function (ev) {
    //   ev.stopPropagation();
    //   if (self.selectedDayClickable) {
    //     if ($(ev.target).hasClass("selected-past-day-time")) {
    //       console.log("past");
    //     } else if ($(ev.target).hasClass("selected-event-day-time")) {
    //       console.log("has class");
    //     } else {
    //       console.log("future empty");
    //     }
    //   }
    // });
  },
  methods: {
    getAgenda(dataFromClient, startTimestamp, minuteLength) {
      let self = this;
      self.isLoaded = true;
      if (minuteLength) {
        self.originTimeLength = minuteLength;
        self.originGridCount = self.originTimeLength / 15;
      }
      if (startTimestamp) {
        self.startFromTimestamp = startTimestamp;
      }
      self.updateData(dataFromClient);
    },
    updateData(dataFromClient) {
      let self = this;
      self.data = JSON.parse(JSON.stringify(dataFromClient));
      lessonTools.addData(0, self.data);
    },
    swipingMiddle(ev) {
      let self = this;
      if (
        self.currentTimestamp >=
          new Date($(".day").eq(self.lessonDays).data("ymd")).getTime() &&
        ev.detail.direction === "right"
      ) {
      } else {
        if (self.moveType === 0) {
          self.moveType =
            ev.detail.direction === "left" || ev.detail.direction === "right"
              ? 2
              : 1;
        }
        if (self.moveType === 2) {
          if (lessonTools.isHorizontal(ev.detail) && self.movable) {
            self.moving(ev.detail.distanceX);
          }
        }
      }
    },
    swipeendMiddle(ev) {
      let self = this;
      if (self.movable) {
        if (
          self.currentTimestamp >=
            new Date($(".day").eq(self.lessonDays).data("ymd")).getTime() &&
          ev.detail.direction === "right"
        ) {
        } else {
          if (self.moveType === 2) {
            self.moveEnd(ev.detail.distanceX);
          }
          self.moveType = 0;
        }
      }
    },
    moveEnd(distanceX) {
      let self = this;
      $("#bottom").css("overflow", "auto");
      let isLeft = distanceX > 0 ? false : true;
      let dis = Math.abs(distanceX);
      let isMoved1 = dis <= self.w / 3 && dis >= self.w / 6;
      let isMoved2 = dis <= self.w / 3 * 2 && dis > self.w / 3;
      let isMoved3 = dis > self.w / 3 * 2;

      if (isMoved1) {
        if (!isLeft) {
          self.marginLeft = self.marginLeft + self.w / 3;
        } else {
          self.marginLeft = self.marginLeft - self.w / 3;
        }
      }
      if (isMoved2) {
        if (!isLeft) {
          self.marginLeft = self.marginLeft + self.w / 3 * 2;
        } else {
          self.marginLeft = self.marginLeft - self.w / 3 * 2;
        }
      }
      if (isMoved3) {
        if (!isLeft) {
          self.marginLeft = self.marginLeft + self.w;
        } else {
          self.marginLeft = self.marginLeft - self.w;
        }
      }

      $("#middleContainer").animate({
        marginLeft: self.marginLeft,
      });
      $("#topContainer").animate(
        {
          marginLeft: self.marginLeft,
        },
        function () {
          if (isMoved1) {
            // length, isLeft, yymmdd, dayType
            lessonTools.updateTableAndDay(
              1,
              isLeft,
              isLeft ? self.lastFormatStr : self.firstFormatStr,
              3
            );

            self.updateMarginLeft();
          }
          if (isMoved2) {
            // length, isLeft, yymmdd, dayType
            lessonTools.updateTableAndDay(
              2,
              isLeft,
              isLeft ? self.lastFormatStr : self.firstFormatStr,
              3
            );

            self.updateMarginLeft();
          }
          if (isMoved3) {
            // length, isLeft, yymmdd, dayType
            lessonTools.updateTableAndDay(
              3,
              isLeft,
              isLeft ? self.lastFormatStr : self.firstFormatStr,
              3
            );

            self.updateMarginLeft();
          }
        }
      );
    },
    moving(distanceX) {
      let self = this;
      $("#middleContainer").css("marginLeft", self.marginLeft + distanceX);
      $("#topContainer").css("marginLeft", self.marginLeft + distanceX);
      $("#bottom").css("overflow", "hidden");
    },
    updateMarginLeft() {
      let self = this;
      // self.marginLeft = - self.w * 3;
      self.marginLeft = -self.w;

      $("#middleContainer").css("marginLeft", self.marginLeft);
      $("#topContainer").css("marginLeft", self.marginLeft);

      let ymd = $(".day.day-3")
          .eq($(".day.day-3").length / 3)
          .data("ymd"),
        timestamp = new Date(ymd).getTime() / 1000;

      // self.getAgenda(self.data);
    },
  },
};
</script>
